.appBackgroundImage {
  background-image: url('https://c0.wallpaperflare.com/preview/576/277/238/bank-notes-banknotes-brazilian-currency-brazilian-money.jpg');
  background-size: cover;
  min-height: 100vh;
}

.headerAlignment {
  text-align: center;
  margin: 5rem 0rem 10rem 0rem !important;
}

.centerAligmnent {
  text-align: center;
}

.textarea {
  width: 68%;
  resize: none;
  max-height: 110px;
  min-height: 100px;
  background-color: #ffffcc;
  font-size: 5em;
  text-align: center;
  overflow: hidden;
  margin-bottom: 5%;
  font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

.body {
  margin: -8px -8px -8px -8px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.input {
  border: 1 px solid #333;
  border-radius: .3rem;
  padding: .25rem;
  width: 7em;
}

.select {
  margin-left: 3vh;
}

.equals {
  font-weight: bold;
  font-size: 2rem;
}

.option {
  height: 30px;
}